/****ALL****/
html, body {
  min-height: 100%;
  font-family: 'Roboto', sans-serif;
  padding: 0;
  margin: 0;
  color: #84888C;
  font-size: .95rem; }
  @media (min-width: 768px) {
    html, body {
      font-size: .85rem; } }
  @media (min-width: 1024px) {
    html, body {
      font-size: .9rem; } }
  @media (min-width: 1320px) {
    html, body {
      font-size: 1rem; } }

* {
  box-sizing: border-box;
  margin: 0;
  padding: 0; }
  *:focus {
    outline: none; }

.azul, h1, .section-3 h2 {
  color: #041F8F; }

.branco, h2, a.button, a.button:hover, .section-4 h2 {
  color: #fff; }

h1 {
  font-size: 2.625rem;
  font-weight: 400; }

h2 {
  font-size: 2rem;
  font-weight: 400; }

p {
  font-weight: 400;
  font-size: 1rem;
  color: #84888C; }

a.button {
  background: #0285C9;
  border-radius: .25rem;
  display: block;
  text-align: center;
  font-size: 1rem;
  line-height: 1.25rem;
  font-weight: 700;
  padding: .75rem 1rem;
  text-decoration: none;
  text-transform: uppercase;
  transition: all .3s ease; }
  @media (min-width: 1024px) {
    a.button {
