.all {
  overflow: hidden; }

.section-1 {
  padding: 2rem 0; }
  .section-1 span {
    display: block;
    padding: .5rem 0;
    color: #0285C9;
    text-align: center; }
    @media (min-width: 678px) {
      .section-1 span {
        text-align: left; } }
  .section-1 img {
    max-width: 100%;
    height: auto; }
  .section-1 h1 strong {
    font-weight: 700;
    display: block; }

.section-2 {
  padding: 2rem 0; }
  @media (min-width: 678px) {
    .section-2 {
      margin-top: 7.5rem; } }
  .section-2 img {
    max-width: 100%;
    margin-top: 5rem; }
    @media (min-width: 1024px) {
      .section-2 img {
        margin-top: -7.5rem; } }
  .section-2 .background {
    background: #0285C9;
    padding: 4rem 2rem 0;
    border-radius: 1rem; }
    @media (min-width: 1024px) {
      .section-2 .background {
