  .section-2 h2 {
    font-weight: 100;
    max-width: 330px;
    margin: 0 0 1rem; }
    .section-2 h2 strong {
      font-weight: 700;
      display: block; }
  .section-2 p {
    color: #fff;
    line-height: 1.375rem; }
  .section-2 a.button {
    background: #041F8F; }
