.section-3 {
  padding: 4rem 0;
  margin: 4rem 0;
  background: #F6F9FC;
  overflow: hidden; }
  .section-3 h2 {
    font-weight: 100; }
    .section-3 h2 strong {
      font-weight: 700; }
  .section-3 p {
    max-width: 600px;
    margin: 0 auto; }
  .section-3 .slider {
    padding: 4rem 0 0; }
    .section-3 .slider .card {
      border: none;
      background: transparent;
      text-align: center; }
      .section-3 .slider .card img {
        max-width: 100%;
        width: auto;
        margin: 0 auto 1rem; }
      .section-3 .slider .card h4 {
        font-size: 1.375rem;
        color: #041F8F;
        margin: 0 auto 1rem; }
    @media (min-width: 1320px) {
      .section-3 .slider .owl-stage-outer {
        width: 100vw; } }
    .section-3 .slider .owl-nav {
      display: block;
      padding: 1rem 0;
      text-align: center; }
    .section-3 .slider .nav {
      text-align: center;
      justify-content: center;
      list-style: center;
      padding: 2rem 0 0; }
      .section-3 .slider .nav span {
        margin: 0 .5rem;
        cursor: pointer; }
      .section-3 .slider .nav .next svg {
        transform: rotate(180deg) translateY(0.5px); }
        .section-3 .slider .nav .next svg path {
          transition: fill .3s ease; }
      .section-3 .slider .nav span:hover svg path {
        fill: #000; }

.section-4 {
  padding: 2rem 0; }
  @media (min-width: 678px) {
    .section-4 {
      margin-top: 7.5rem; } }
  .section-4 .background {
    background: #0285C9;
    padding: 2rem 2rem;
    border-radius: 1rem; }
    @media (min-width: 678px) {
      .section-4 .background {
        margin: 5rem 0; } }
    @media (min-width: 1024px) {
      .section-4 .background {
        padding: 2rem 4rem; } }
    .section-4 .background p {
      max-width: 380px;
      margin: 1rem 0; }
  .section-4 h2 {
    font-weight: 700; }
  .section-4 p {
    max-width: 600px;
    margin: 0 auto;
    color: #fff; }
  .section-4 .calculadora {
    padding: 2rem;
    background: #fff;
    border-radius: .25rem;
    box-shadow: 0 0 6px rgba(0, 0, 0, 0.25);
    max-width: 520px; }
    @media (min-width: 1024px) {
      .section-4 .calculadora {
        margin-top: -5rem;
        margin-bottom: -5rem; } }
    .section-4 .calculadora h4 {
      color: #0285C9;
      font-size: 2rem;
      font-weight: 700; }
    .section-4 .calculadora p {
      font-size: 1rem;
      color: #84888C; }
    .section-4 .calculadora label {
      font-size: 1rem;
      color: #2F2F2F;
      display: block;
      margin: 1.5rem 0 1.5rem;
      font-weight: 500; }
    .section-4 .calculadora .btn {
      background: #041F8F;
      font-size: 1rem;
      text-transform: uppercase;
      padding: .75rem 2rem;
      border: none;
      border-radius: .5rem;
      font-weight: 700; }
    .section-4 .calculadora form {
      padding: 2rem 0; }
      .section-4 .calculadora form .slider-container {
        display: inline-block;
        position: relative;
        width: 100%;
        margin: 1rem 0;
        position: relative; }
        .section-4 .calculadora form .slider-container:before, .section-4 .calculadora form .slider-container:after {
          display: block;
          position: absolute;
          bottom: -25px; }
        .section-4 .calculadora form .slider-container:before {
          content: "10";
          left: 0; }
        .section-4 .calculadora form .slider-container:after {
          content: "+1000";
          right: 0; }
      .section-4 .calculadora form input[type="number"], .section-4 .calculadora form input[type="text"] {
        font-size: 1rem; }
      .section-4 .calculadora form input[type="range"] {
        position: relative;
        z-index: 1000;
        -webkit-appearance: none;
        -moz-appearance: none;
        appearance: none;
        background: transparent;
        height: 1px;
        background: #84888C;
        outline: none;
        margin: 0;
        padding: 0;
        width: 100%; }
      .section-4 .calculadora form input[type="range"]::-webkit-slider-thumb {
        position: relative;
        -webkit-appearance: none;
        appearance: none;
        width: 15px;
        height: 15px;
        background: #fff;
        border: 2px solid #0285C9;
        border-radius: 100%;
        cursor: pointer;
        z-index: 5000;
        -webkit-transition: background 0.3s ease-in-out;
        transition: background 0.3s ease-in-out; }
      .section-4 .calculadora form input[type="range"]::-webkit-slider-thumb:hover,
      .section-4 .calculadora form input[type="range"]::-webkit-slider-thumb:active {
        background: #fff;
        border: 2px solid #0285C9;
        -webkit-transition: background 0.3s ease-in-out;
        transition: background 0.3s ease-in-out; }
      .section-4 .calculadora form input[type="range"]::-moz-range-track {
        background: transparent; }
      .section-4 .calculadora form input[type="range"]::-moz-range-thumb {
        position: relative;
        -moz-appearance: none;
        appearance: none;
        width: 15px;
        height: 15px;
        background: #fff;
        border: 1px solid #0285C9;
        border-radius: 100%;
        cursor: pointer;
        z-index: 5000;
        -moz-transition: background 0.3s ease-in-out;
        transition: background 0.3s ease-in-out; }
      .section-4 .calculadora form input[type="range"]::-moz-range-thumb:hover,
      .section-4 .calculadora form input[type="range"]::-moz-range-thumb:active {
        background: #165dbb;
        -moz-transition: background 0.3s ease-in-out;
        transition: background 0.3s ease-in-out; }
      .section-4 .calculadora form .fill {
        position: absolute;
        z-index: 50;
        bottom: 5px;
        height: 1px;
        width: 0%;
        border-radius: 6px;
        background: #0285C9; }
      .section-4 .calculadora form .count {
        position: absolute;
        top: -30px;
        left: 0;
        background: #303D63;
        color: #fff;
        padding: .25rem 0;
        border-radius: .2rem;
        text-align: center;
        width: 50px;
        font-size: .8rem; }
        .section-4 .calculadora form .count:after {
          width: 4px;
          height: 4px;
          background: #303D63;
          position: absolute;
          bottom: -2px;
          left: 50%;
          margin-left: -2px;
          transform: rotate(45deg); }

.section-5 {
  padding: 2rem 0; }
  .section-5 h3 {
