    color: #041F8F;
    font-weight: 700;
    margin: 2rem 0; }
  .section-5 .card {
    border: none;
    position: relative;
    border-radius: .25rem;
    overflow: hidden;
    display: block;
    margin: 1rem 0; }
    .section-5 .card img {
      display: block;
      max-width: 100%;
      height: auto; }
    .section-5 .card .content {
      position: absolute;
      padding: 4rem 2rem 1rem;
      bottom: 0;
      left: 0;
      background: -moz-linear-gradient(top, rgba(0, 26, 77, 0) 0%, #001a4d 62%, #001a4d 100%);
      background: -webkit-linear-gradient(top, rgba(0, 26, 77, 0) 0%, #001a4d 62%, #001a4d 100%);
      background: linear-gradient(to bottom, rgba(0, 26, 77, 0) 0%, #001a4d 62%, #001a4d 100%);
      filter: progid:DXImageTransform.Microsoft.gradient( startColorstr='#00001a4d', endColorstr='#001a4d',GradientType=0 ); }
      .section-5 .card .content h3, .section-5 .card .content p {
        color: #fff; }
      .section-5 .card .content h3 {
        margin: 0 0 1rem;
        font-size: 1.375rem; }

footer {
  padding: 0 0 2rem; }
  @media (max-width: 678px) {
