        line-height: 32px;
        text-align: center;
        text-decoration: none;
        transition: background .3s ease; }
        footer .social li a i {
          color: #fff; }
        footer .social li a:hover {
          background: #041F8F; }

#hidden-content .resultado {
  text-align: center;
  border-radius: .5rem;
  background: #fff;
  max-width: 340px; }
  #hidden-content .resultado h3 {
    font-size: 1.375rem; }
  #hidden-content .resultado h4 {
    font-size: 1.25rem;
    color: #165dbb;
